@import url(https://fonts.googleapis.com/css?family=Assistant:200,300,400,600,700,800|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.principal .titulo {
  font-size: 3rem;
  font-weight: 600;
  font-family: "Assistant", sans-serif;
  color: #5d5d62;
  margin: 2rem;
  border-bottom: 2px solid #999999;
  padding-bottom: 0.5rem; }

.principal .botones .boton {
  font-size: 1.5rem;
  margin: 0.2rem;
  color: #707070; }
  .principal .botones .boton:hover {
    cursor: pointer; }

.principal .botones p {
  font-size: 1rem;
  font-weight: 300;
  font-family: 'Roboto', sans-serif;
  color: #8d969c;
  font-size: 1.5rem;
  margin-top: 0.9rem;
  margin-right: 1rem;
  margin-left: 2rem; }

.principal .botones .seleccionado {
  color: #00a8eb;
  font-size: 1.7rem; }

.post {
  margin-bottom: 3rem; }
  .post .imagen img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center; }
  .post .contenido a {
    font-size: 2rem;
    font-weight: bold;
    font-family: 'Assistant', sans-serif;
    color: #5d5d62;
    margin-bottom: 0.5rem; }
  .post .contenido p {
    font-size: 1rem;
    font-weight: 300;
    font-family: 'Roboto', sans-serif;
    color: #8d969c; }
  .post .contenido .autorVotos {
    padding: 0;
    margin-left: 0rem; }
    .post .contenido .autorVotos .autor {
      display: flex;
      align-items: center; }
      .post .contenido .autorVotos .autor h2 {
        font-size: 1.2rem;
        font-weight: regular;
        font-family: 'Assistant', sans-serif;
        color: #5d5d62;
        margin-right: 1rem; }
      .post .contenido .autorVotos .autor img {
        width: 3rem;
        height: 3rem;
        border-radius: 1.5rem;
        -webkit-border-radius: 1.5rem;
        -moz-border-radius: 1.5rem;
        -ms-border-radius: 1.5rem;
        -o-border-radius: 1.5rem; }
  .post .contenido .votos {
    display: flex;
    flex-flow: row wrap;
    justify-content: start;
    align-items: center; }
    .post .contenido .votos p {
      font-size: 1.2rem;
      font-weight: regular;
      font-family: 'Assistant', sans-serif;
      color: #5d5d62;
      font-weight: bold;
      margin-top: 1em;
      color: #F57B30;
      margin: 0.5em;
      font-size: 1.5em; }
    .post .contenido .votos i {
      font-size: 1.2em;
      color: #adadad; }
      .post .contenido .votos i:hover {
        cursor: pointer; }

